.FooterApp{
    background: black;
    height: 150px;
    width: 100%;

    margin-top: 50px;
    color: white;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}



.FooterApp-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    width: 90%;

    margin-top: 20px;

    /*border: 1px solid white;*/
}

.FooterApp-container h1{
    display: flex;

}

.icon-footer-container{
    display: flex;
    flex-direction: row;
    justify-self: center;
    width: 60%;
    /*border: 1px solid red;*/

    column-gap: 20px;

    margin-bottom: 20px;
}

.icon-footer{
    font-size: 50px;
    color: white;
}

.icon-footer:hover{
    font-size: 50px;
    color: gray;
}